















import { Component, Vue, Prop } from 'vue-property-decorator';
import { mapActions } from 'vuex';

import SinglePost from "@/components/Blog/Post.vue";
import NavBar from '@/components/Navbar.vue'; // @ is an alias to /src
import HomeNav from '@/components/HomeNav.vue'; // @ is an alias to /src
import List from '@/components/Blog/PostList.vue';

@Component({
    components: {
        SinglePost,
        NavBar,
        HomeNav,
        List,
    }
})
export default class Post extends Vue {
    @Prop() private postUid!: string;
    private mounted () {
        this.$store.dispatch("GetAndSetPost", this.$route.params.uid);
    }
}
