










import { Document } from '@/prismic/api';
import { Component, Vue, Prop } from 'vue-property-decorator';
import moment from 'moment';
import NavBar from '@/components/Navbar.vue'; // @ is an alias to /src
import HomeNav from '@/components/HomeNav.vue'; // @ is an alias to /src
import PrismicDOM from 'prismic-dom';

@Component({
    components: {
    },
})
export default class PostList extends Vue {

    private PostHtml(post: object): string {
        console.log(post)
        return PrismicDOM.RichText.asHtml(post);
    }

    private get content(): string {
        return this.PostHtml(this.post.data.post_content)
    }
    private get post(): Document {
        return this.$store.state.CurrentPost;
    }
}
